import React from "react";
import { Route, Switch } from "react-router-dom"
import Home from "./containers/Home";
import Login from "./containers/Login";
import SignUp from "./containers/SignUp";
import Admin from "./containers/admin/admin";
import Electrical from "./containers/tabs/Electrical"
import Information from "./containers/tabs/Information";
import Status from "./containers/tabs/Status";
import Parameters from "./containers/tabs/Parameters";
import Performance from "./containers/tabs/Performance";
export default function Routes() {
    return (
        <Switch>
            <Route exact path="/">
                <Home />
            </Route>
            <Route exact path="/login">
                <Login />
            </Route>
            <Route exact path="/sign_up">
                <SignUp />
            </Route>
            <Route exact path="/admin">
                <Admin />
            </Route>
            <Route exact path="/admin/login">
                <Login />
            </Route>
            <Route exact path="/info">
                <Information />
            </Route>
            <Route exact path="/electrical">
                <Electrical />
            </Route>
            <Route exact path="/status">
                <Status />
            </Route>
            <Route exact path="/performance">
                <Performance />
            </Route>
            <Route exact path="/parameters">
                <Parameters />
            </Route>
        </Switch>
    )
}