import { Button, Card, CardContent, FormControl, InputLabel, Select, MenuItem } from '@material-ui/core'
import React from 'react'

class TechnicianHome extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            customerFilter : "",
            machineFilter : ""
        }
    }
    handleCustomerFilter = (event) => {
        this.setState({customerFilter : event.target.value})
    }
    handleMachineFilter = (event) => {
        this.setState({machineFilter : event.target.value})
    }
    handleClear = (event) => {
        this.setState({
            customerFilter : "",
            machineFilter : ""
        })
    }

    render() {
        console.log(this.state.customerFilter, this.state.machineFilter);
        return(
            <div style={{margin:"20px"}}>
                <div style={{display:"flex"}}>
                    <Card variant="outlined">
                        <CardContent style={{padding:"8px"}}>Home</CardContent>
                    </Card>
                    <div style={{flexGrow:"1"}}></div>
                    <Button variant="outlined" style={{marginRight:"20px"}}>MY MACHINES</Button>
                    <Button variant="outlined">REGISTER MACHINE</Button>
                </div>
                <div style={{display:"flex"}}>
                    <Card variant="outlined" style={{ width:"100%", margin:"30px 0 30px 0", borderRadius:"10px"}}>
                        <Card style={{height:"50px", width:"100%", borderRadius:"10px 10px 0 0", display:"flex", paddingRight:"40px", columnGap:"40px"}}>
                                <div style={{display:"flex", padding:"10px"}}>Filter By:</div>
                                <FormControl>
                                    <InputLabel style={{width:"200px", fontSize:"10px"}} id="filter-customer">CUSTOMER</InputLabel>
                                    <Select style={{width:"200px", fontSize:"13px", paddingTop:"0"}} labelId="filter-customer" value={this.state.customerFilter} onChange={this.handleCustomerFilter}>
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value={10}>Ten</MenuItem>
                                    <MenuItem value={20}>Twenty</MenuItem>
                                    <MenuItem value={30}>Thirty</MenuItem>
                                    </Select>
                                </FormControl>
                                <FormControl>
                                    <InputLabel style={{width:"200px", fontSize:"10px"}} id="filter-customer">MACHINE</InputLabel>
                                    <Select style={{width:"200px", fontSize:"13px", paddingTop:"0"}} labelId="filter-customer" value={this.state.machineFilter} onChange={this.handleMachineFilter}>
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value={10}>Ten</MenuItem>
                                    <MenuItem value={20}>Twenty</MenuItem>
                                    <MenuItem value={30}>Thirty</MenuItem>
                                    </Select>
                                </FormControl>
                                <Button variant="text" onClick={this.handleClear}>CLEAR</Button>
                        </Card>
                        <div style={{height:"100px"}}></div>
                    </Card>
                </div>
            </div>
        )
    }
}

export default TechnicianHome