import React, { Children, useEffect, useState } from 'react';
import app from "../../FirebaseConfig";
import firebase from "firebase/app";
import "firebase/auth"
// import database from "firebase/database"
import { Redirect } from 'react-router-dom';
import { Card, CardHeader, TextField } from '@material-ui/core';
import Firebase from 'firebase';

export default function Admin() {
    const[userEmail, setUserEmail] = useState("");
    const[authCode, setAuthCode] = useState("");
    var db = Firebase.database().ref("authCode");
    var data = {};
    // firebase.auth().onAuthStateChanged(function(user) {
    //     if (user) 
    //     setUserEmail(user.email)
    // })
    function handelClick() {
        const min = 1;
        const max = 100;
        const rand = Math.floor(100000 + Math.random() * 900000);
        setAuthCode(rand);
        data[rand] = userEmail;
        db.child("technician").update(data);
    }
    return (
        <div>welcome to admin {userEmail} 
            <Card>
                <CardHeader title="Register a Technisian"></CardHeader>
                <TextField id="email" type="email" label="Email" value={userEmail} onChange={(e) => {setUserEmail(e.target.value)}} variant="outlined"></TextField>
                <TextField id="authCode" type="email" label="Auth Code" variant="outlined" disabled value={authCode}></TextField>
                <button variant="" onClick={handelClick}>Generate Auth Code</button>
            </Card>
        </div>
    )
    
}