import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import Routes from "./routes";
import AppBar from '@material-ui/core/AppBar'
import { Avatar, Button, Toolbar, MenuList, MenuItem } from '@material-ui/core';
import { Link, Redirect, useHistory, useLocation, withRouter } from 'react-router-dom'
import app from './FirebaseConfig'
import firebase from 'firebase/app'
import 'firebase/auth';


function App() {
  const history = useHistory();
  const[userAuthenticated, setUserAutenticated] = useState("");
  const [locationMatch, setLocationMatch] = useState("");
  const[homeloc, setHomeloc] = useState("");
  const location = useLocation();
  const[userEmail, setUserEmail] = useState("");
  const[userName, setUserName] = useState("XYZ");
  const [tab, setTab] = useState("INFORMATION");
  const[userType, setUserType] = useState("");
  useEffect(() => {onLoad()},[]);

  const handleChange = (event) => {
    document.getElementsByClassName("menu-active")[0].classList.remove("menu-active");
    event.target.classList.add("menu-active");
    setTab(event.target.getAttribute("label"));
  };
  function onLoad () {
    firebase.auth().onAuthStateChanged(function(user) {
      if (user) {
        setUserAutenticated(true);
        setUserEmail(user.email);
        setUserName(user.displayName);
        firebase.database().ref(`users/${user.uid}`).once('value').then(function(snapShot) {
          setUserType(snapShot.val().accountType);
        })
        // User is signed in.
      } else {
        setUserAutenticated(false);
        (location.pathname === "/admin") ? setLocationMatch(true) : setLocationMatch(false);
        (location.pathname === "/") ? setHomeloc(true) : setHomeloc(false);
        // No user is signed in.
      }
    });
  }

  async function handleLogout() {
    await firebase.auth().signOut();
    setUserAutenticated(false);
    (location.pathname === "/admin") ? history.push("/admin/login") : history.push("/login");
  }

  var buttons = locationMatch ? <Redirect to="admin/login" />  : <div><Button color="inherit" component={Link} to="/login">Login</Button><Button color="inherit" component={Link} to="/sign_up">Sign Up</Button></div> ;
  var avatar = <Avatar alt={userEmail} src="/avatar.jpg"></Avatar>
  var logdinBar = <div style={{display:"flex"}}>
                    <div display="flex" style={{marginRight:"10px"}}>
                      <p style={{margin:"0", textAlign:"right", }}><b>{userName}</b></p>
                      <Button style={{padding:"0"}} variant="text" color="inherit" onClick={handleLogout} disableElevation size="small" className="button-logout">Log Out</Button> 
                    </div>
                    {avatar} 
                  </div>;
  if (userAuthenticated !== "") {
    var TabName = "IFORMATION"
    switch (tab) {
      case "INFORMATION":
        TabName = <Redirect to={{pathname : "/info", state: { id : userType}}} />;
        break;
      case "STATUS":
        TabName = <Redirect to="/status" />;
        break;
      case "PERFORMANCE":
        TabName = <Redirect to="/performance" />;
        break;
      case "ELECTRICAL SUPPLY":
        TabName = <Redirect to="/electrical" />
        break;
      case "PARAMETERS":
        TabName = <Redirect to="/parameters" />
        break;

      default:
        break;
    }
    return (
      <div>
      <AppBar position="static" style={{color:"black", backgroundColor:"white", display:"flex", flexDirection:"row"}}>
        <div style={{marginRight:"50px"}}>LOGO</div>
      {userAuthenticated ? 
          <MenuList  style={{flexDirection:"row", display:"flex", flexGrow:"1"}} onClick={handleChange}>
            <MenuItem className="menuButton menu-active" label="INFORMATION">INFORMATION</MenuItem>
            <MenuItem className="menuButton" label="STATUS">STATUS</MenuItem>
            <MenuItem className="menuButton" label="PERFORMANCE">PERFORMANCE</MenuItem>
            <MenuItem className="menuButton" label="ELECTRICAL SUPPLY">ELECTRICAL SUPPLY</MenuItem>
            <MenuItem className="menuButton" label="PARAMETERS">PARAMETERS</MenuItem>
          </MenuList>
        : <div style={{flexGrow:"1"}}></div>}

        <Toolbar>
          {userAuthenticated ? logdinBar : buttons}  
        </Toolbar>
        
      </AppBar>
      {homeloc ? <Redirect to="/login" /> : <div></div>}
      <Routes />
      <div>
        {TabName}
      </div>
      </div>
    )
  } else {
    return (
    <div></div>
    )
  }
}

export default App;
