import { Card } from '@material-ui/core'
import React from 'react'
import TechnicianHome from './subtabs/technicianHome'
import ClientHome from './subtabs/clientHome'
import {withRouter} from 'react-router-dom';

class Information extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            userType : null
        }
    }
    componentDidMount(props){
        this.setState({userType : this.props.location.state.id});
    }
    render() {
        if (this.state.userType !== "") {
            return(
                <div style={{display:"flex", margin:"20px"}}>
                    <Card style={{width:"100%", height:"80vh"}}>
                        {(this.state.userType === "Technician") ? <TechnicianHome /> : <ClientHome />}    
                    </Card>
                </div>
            )
        }else {
            return(
                <div></div>
            )
        }
    }
}

export default withRouter(Information);