import React, { useState } from "react"
import 'firebase/auth'
import Card from '@material-ui/core/Card';
import { Button, CardContent, CardHeader, FormControlLabel, Radio, RadioGroup, TextField } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert"
import app from '../FirebaseConfig'
import { useHistory} from 'react-router-dom'
import './SignUp.css'
import firebase from "firebase/app"

export default function SignUp() {
    const[name, setName] = useState("");
    const[email, setEmail] = useState("");
    const[password, setPassword] = useState("");
    const[phone, setPhone] = useState("");
    const[error, setError] = useState("");
    const[cPassword, setCpassword] = useState("");
    const[authCode, setAuthCode] = useState("");
    const[typeValue, setTypeValue] = useState("company");
    var db = firebase.database();
    var firestoreDB = firebase.firestore();
    var auth = app.auth();
    const history = useHistory();
    async function handleSignup(event) {
        event.preventDefault();
        setError("")        
        try {
            (db.ref("authCode").child(typeValue).once("value")).then(function(dataSnapshot) {
                dataSnapshot.forEach(function(childSnapshot) {
                    if (typeValue === "technician") {
                        if (childSnapshot.key === authCode && childSnapshot.val() === email) {
                            auth.createUserWithEmailAndPassword(email,password).then(function(result) {
                                const user = auth.currentUser;
                                return user.updateProfile(
                                    {displayName: name}
                                )
                            });
                            auth.onAuthStateChanged(function(user){
                                if(user) {
                                    var docRef = firestoreDB.collection('Technicians').doc(user.uid);
                                    var userData = {"name": name, "email": email, "phone": phone};
                                    docRef.set(userData);
                                    db.ref("authCode").child(typeValue).child(authCode).remove();
                                    history.push("/");
                                }
                            })
                        }
                    } else {
                        var companyId = childSnapshot.key;
                        childSnapshot.forEach(async function(childValues) {
                            if (childValues.key === authCode && childValues.val() === email) {
                                auth.createUserWithEmailAndPassword(email,password);
                                await auth.onAuthStateChanged(function(user) {
                                    if (user) {
                                        var docRef = firestoreDB.collection('Companies').doc(companyId);
                                        var userData = {"name": name, "email": email, "phone": phone};
                                        console.log(userData)
                                        docRef.get().then(function(doc) {
                                            if (doc.exists) {
                                                docRef.collection("User").doc(user.uid).set(userData)
                                            }    
                                        });
                                        db.ref("authCode").child(typeValue).child(companyId).child(authCode).remove();
                                        history.push("/");
                                    }
                                })
                            }
                        })
                    }
                })
            })
        } catch (e) {
            console.log(e);
            if (e.message === "The email address is already in use by another account.") { setError("email already in use.");}
        }
    }
    const handleOption = (event) => {
        setTypeValue(event.target.value);
    }
    return(
        <div style={{display:"flex", alignItems:"center", justifyContent:"center", marginTop:"20px"}}>
            <Card style={{display: "flex", width:"fit-content"}}>
                <CardContent>
                <CardHeader color="black" title="Sign Up"></CardHeader>
                <form style={{display:"flex", flexDirection:"column"}} onSubmit={handleSignup}>
                    <TextField id="name" value={name} onChange={(e) => setName(e.target.value)} variant="outlined" label="Name" style={{margin:"8px"}}></TextField>
                    <TextField id="Phone" value={phone} onChange={(e) => setPhone(e.target.value)} variant="outlined" label="Phone" style={{margin:"8px"}}></TextField>
                    <TextField id="email" value={email} onChange={(e) => setEmail(e.target.value)} variant="outlined" label="Email" style={{margin:"8px"}}></TextField>
                    <div style={{margin:"10px"}}>
                        <RadioGroup row aria-label="Type" value={typeValue} onChange={handleOption}>
                            <label style={{margin:"20px 10px"}}>Type</label>
                            <FormControlLabel value="company" control={<Radio />} label="Client" style={{margin:"8px"}}></FormControlLabel>
                            <FormControlLabel value="technician" control={<Radio />} label="Technician" style={{margin:"8px"}}></FormControlLabel>
                        </RadioGroup>
                    </div>
                    <TextField id="authCode" value={authCode} onChange={(e) => setAuthCode(e.target.value)} variant="outlined" label="Auth Code" style={{margin:"10px"}}></TextField>
                    <div display="flex">
                        <TextField id="password" type="password" value={password} onChange={(e) => setPassword(e.target.value)} variant="outlined" label="Password" style={{margin:"10px"}}></TextField>
                        <TextField id="cPassword" type="password" value={cPassword} onChange={(e) => setCpassword(e.target.value)} variant="outlined" label="Confirm Password" style={{margin:"10px"}}></TextField>
                    </div>
                    {error ? <Alert severity="error" style={{margin:"10px", fontSize:"10px", height:"15px"}}>{error}</Alert> : <Alert severity="error" style={{display:"none", margin:"10px"}}>Failed</Alert>}
                    <Button variant="outlined" type="submit" style={{margin:"10px"}}>Sign Up</Button>
                </form>
                </CardContent>
            </Card>
        </div>
    )
}